html {
  height: 100%;
}

body {
  background-color: #333;
  color: #eee;
  font-family: "Rubik", sans-serif;
  margin: 0;
  height: 100%;
}
body > main {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}
body > main > header {
  flex: 0 0 3rem;
}
body > main > header ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
body > main > header ol > li {
  margin: 0 0.2rem;
  display: inline-block;
  width: 2rem;
  height: 2rem;
  border-radius: 2rem;
  text-align: center;
  line-height: 2rem;
  transition: background-color 0.2s;
}
body > main > header ol > li.pass {
  background-color: green;
}
body > main > header ol > li.current {
  border: solid 1px #eee;
}
body > main > main {
  flex: 1 0;
}
body > main > footer {
  flex: 0 0 5rem;
  background-color: #000;
}